import { motion } from "framer-motion";
import { useRef } from "react";
import React from "react";
import styled from "styled-components";
import { LeftArrow, RightArrow } from "./Arrows";
import { ScrollMenu, VisibilityContext } from "react-horizontal-scrolling-menu";
import usePreventBodyScroll from "../usePreventBodyScroll";
import Reflection from "../components/Reflection";
import Item from "../components/Item";
import PrizeItem from "../components/PrizeItem";
import RunRight from "../assets/images/run-right.gif";
import RunLeft from "../assets/images/run-left.gif";
import PlayButtonImage from "../assets/images/button-play.png";
import Clap from "../assets/images/clap-animation.png";
import MainImg from "../assets/images/main.png";
import FiftPrize from "../assets/images/fifth-prize.png";
import FourthPrize from "../assets/images/fourth-prize.png";
import ThirdthPrize from "../assets/images/third-prize.png";
import SecondPrize from "../assets/images/second-prize.png";
import FirstPrize from "../assets/images/first-prize.png";
import FifthOne from "../assets/images/winning-work/5-1.png";
import FifthTwo from "../assets/images/winning-work/5-2.png";
import FifthThree from "../assets/images/winning-work/5-3.png";
import FifthFour from "../assets/images/winning-work/5-4.png";
import FifthFive from "../assets/images/winning-work/5-5.png";
import FourthOne from "../assets/images/winning-work/4-1.png";
import FourthOneVideo from "../assets/images/winning-work/4-1.mp4";
import FourthTwo from "../assets/images/winning-work/4-2.png";
import FourthTwoVideo from "../assets/images/winning-work/4-2.mp4";
import FourthThree from "../assets/images/winning-work/4-3.png";
import ThirdOne from "../assets/images/winning-work/3-1.png";
import ThirdTwo from "../assets/images/winning-work/3-2.png";
import ThirdTwoVideo from "../assets/images/winning-work/3-2.mp4";
import Second from "../assets/images/winning-work/2.png";
import SecondVideo from "../assets/images/winning-work/2.mp4";
import First from "../assets/images/winning-work/1.png";

const SliderWrap = styled.div`
  position: relative;
  display: flex;
  list-style: none;
  height: calc(100vh - 173px);
  overflow-x: auto;
  padding: 20px 0 0;
  margin: 0 auto;
  @media (max-width: 760px) {
  }
`;

const StartPage = styled(motion.div)`
  position: relative;
  display: flex;
  width: 100vw;
  height: calc(100vh - 30vh);
  overflow: hidden;
`;

const ImageWrap = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100vw;
  height: 100%;
  flex: 1 1;
  @media (max-width: 760px) {
    /* margin: 0 20vw; */
  }
`;

const MainImage = styled(motion.img)`
  position: relative;
  object-fit: contain;
  @media (max-width: 760px) {
    left: -50%;
    height: auto;
    width: 200%;
  }
`;

const PlayButton = styled(motion.img)`
  position: absolute;
  width: 80px;
  height: auto;
  top: 230px;
  opacity: 0.8;
  cursor: pointer;
  &:hover {
    opacity: 1;
  }
`;

const Video = styled(motion.video)`
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -65%);
  width: 35vw;
  height: 35vw;
  @media (max-width: 760px) {
    max-width: 50%;
  }
`;

const PointerIllu = styled.img`
  position: fixed;
  top: auto;
  bottom: 0;
  left: 2%;
  width: 20rem;
  height: fit-content;
  object-fit: contain;
  @media screen and (max-width: 760px) {
    width: 7rem;
    left: 50%;
    transform: translateX(-50%);
  }
`;

const ClapImage = styled(motion.img)`
  position: absolute;
  right: 20%;
  bottom: 5vh;
  max-width: 10vw;
  height: auto;
  @media screen and (max-width: 760px) {
    max-width: 20vw;
  }
`;

type scrollVisibilityApiType = React.ContextType<typeof VisibilityContext>;

export default function Slider() {
  const { disableScroll, enableScroll } = usePreventBodyScroll();
  const runKeykney = useRef(null);
  const RefFourthOneVideo = useRef(null);

  function onWheel(
    apiObj: scrollVisibilityApiType,
    ev: React.WheelEvent
  ): void {
    const isThouchpad = Math.abs(ev.deltaX) !== 0 || Math.abs(ev.deltaY) < 15;
    const currentImg: any = runKeykney.current;

    if (isThouchpad) {
      ev.stopPropagation();
      return;
    }

    if (ev.deltaY < 0) {
      currentImg.src = RunRight;
      apiObj.scrollNext();
    } else if (ev.deltaY > 0) {
      currentImg.src = RunLeft;
      apiObj.scrollPrev();
    }
  }

  function videoControl(e: any) {
    const videoElement = e.target.nextSibling;
    videoElement.style.display = "block";
    videoElement.play();
    if (videoElement.requestFullScreen) {
      videoElement.requestFullScreen();
    } else if (videoElement.webkitRequestFullScreen) {
      videoElement.webkitRequestFullScreen();
    } else if (videoElement.mozRequestFullScreen) {
      videoElement.mozRequestFullScreen();
    }
  }

  function videoStopOnWhell(e: any) {
    const videoElement = e.target.querySelector("video");
    videoElement.pause();
    videoElement.style.display = "none";
  }

  return (
    <SliderWrap className='example'>
      <div
        onMouseEnter={disableScroll}
        onMouseLeave={enableScroll}
        style={{ width: "100vw", height: "fit-content" }}>
        <ScrollMenu
          onWheel={onWheel}
          LeftArrow={LeftArrow}
          RightArrow={RightArrow}>
          <StartPage>
            <MainImage
              src={MainImg}
              animate={{
                scale: [0.5, 1, 1, 0.5, 1],
              }}
              transition={{
                duration: 2,
                ease: "easeInOut",
                times: [0, 0.2, 0.5, 0.8, 1],
                repeatDelay: 1,
              }}
            />
          </StartPage>

          <StartPage>
            <ImageWrap key='5-prize'>
              <PrizeItem src={FiftPrize} />
            </ImageWrap>
          </StartPage>
          <ImageWrap key='5-1'>
            <Item src={FifthOne} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='5-2'>
            <Item src={FifthTwo} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='5-3'>
            <Item src={FifthThree} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='5-4'>
            <Item src={FifthFour} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='5-5'>
            <Item src={FifthFive} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>

          <StartPage>
            <ImageWrap key='5-prize'>
              <PrizeItem src={FourthPrize} />
            </ImageWrap>
          </StartPage>
          <ImageWrap key='4-1' onWheel={videoStopOnWhell}>
            <Item src={FourthOne} />
            <PlayButton src={PlayButtonImage} onClick={videoControl} />
            <Video controls loop ref={RefFourthOneVideo}>
              <source src={FourthOneVideo} type='video/mp4' />
            </Video>
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='4-2' onWheel={videoStopOnWhell}>
            <Item src={FourthTwo} />
            <PlayButton src={PlayButtonImage} onClick={videoControl} />
            <Video controls loop>
              <source src={FourthTwoVideo} type='video/mp4' />
            </Video>
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='4-3'>
            <Item src={FourthThree} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>

          <StartPage>
            <ImageWrap key='5-prize'>
              <PrizeItem src={ThirdthPrize} />
            </ImageWrap>
          </StartPage>
          <ImageWrap key='3-1'>
            <Item src={ThirdOne} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
          <ImageWrap key='3-2' onWheel={videoStopOnWhell}>
            <Item src={ThirdTwo} />
            <PlayButton src={PlayButtonImage} onClick={videoControl} />
            <Video controls loop>
              <source src={ThirdTwoVideo} type='video/mp4' />
            </Video>
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>

          <StartPage>
            <ImageWrap key='5-prize'>
              <PrizeItem src={SecondPrize} />
            </ImageWrap>
          </StartPage>
          <ImageWrap key='2' onWheel={videoStopOnWhell}>
            <Item src={Second} />
            <PlayButton src={PlayButtonImage} onClick={videoControl} />
            <Video controls loop>
              <source src={SecondVideo} type='video/mp4' />
            </Video>
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>

          <StartPage>
            <ImageWrap key='5-prize'>
              <PrizeItem src={FirstPrize} />
            </ImageWrap>
          </StartPage>
          <ImageWrap key='1'>
            <Item src={First} />
            <ClapImage src={Clap} />
            <Reflection />
          </ImageWrap>
        </ScrollMenu>
      </div>
      <PointerIllu ref={runKeykney} src={RunRight}></PointerIllu>
    </SliderWrap>
  );
}
