import { motion } from "framer-motion";
import styled from "styled-components";

const PrizeImage = styled(motion.img)`
  position: relative;
  width: 50vw;
  @media (max-width: 760px) {
    height: auto;
    width: 100%;
  }
`;

export default function PrizeItem(obj: any) {
  return (
    <PrizeImage
      src={obj.src}
      transition={{
        duration: 1,
        ease: "easeInOut",
        times: [1, 1.2, 1, 0.8, 0.5, 0.2, 0.5, 0.2, 1],
        repeat: Infinity,
        repeatDelay: 1,
      }}
      initial={{ scale: 0 }}
      whileInView={{
        scale: [1.2, 1, 0.8, 0.5, 0.3, 0.5, 0.3, 0.2, 0.3, 1],
      }}
    />
  );
}
