import { motion } from "framer-motion";
import styled from "styled-components";
import ReflectioImage from "../assets/images/reflection.png";

const ReflectionWrap = styled(motion.div)`
  position: absolute;
  top: 10px;
  left: 63%;
  width: 150px;
  height: 150px;
  transform: rotate(25deg) translateX(-50%);
  @media screen and (max-width: 760px) {
    width: 100px;
    height: 100px;
  }
`;

export default function Reflection() {
  return (
    <ReflectionWrap
      transition={{
        duration: 1.5,
        ease: "easeInOut",
        repeat: Infinity,
        repeatDelay: 0.5,
      }}
      initial={{ opacity: 0.5, scale: 0.5 }}
      whileInView={{ opacity: 1, scale: 1, rotate: [20, 0, 20, 10, 20] }}>
      <img src={ReflectioImage} alt='따라란 축하합니다' />
    </ReflectionWrap>
  );
}
